import React, { useState } from "react";
import { toast } from "react-toastify";
import { Link } from "react-router-dom";

import { useDispatch, useSelector } from "react-redux";
// import CheckoutSteps from "../components/CheckoutSteps";
import { saveShippingAddress } from "../actions/cartAction";

import { FaBackward } from "react-icons/fa";
import { BiLogInCircle } from "react-icons/bi";
import CheckOutSteps from "../components/CheckOutSteps";

const Shipping = ({ history }) => {
  const dispatch = useDispatch();
  const { shippingAddress } = useSelector((state) => state.cart);

  const [address, setAddress] = useState(shippingAddress.address);
  const [city, setCity] = useState(shippingAddress.city);
  const [postalCode, setPostalCode] = useState(shippingAddress.postalCode);
  const [country, setCountry] = useState("Pakistan");
  const [phoneNumber, setphoneNumber] = useState(shippingAddress.phoneNumber);

  const submitHandler = (e) => {
    e.preventDefault();
    if (postalCode.length > 5 || postalCode.length < 5) {
      toast.error(`Please enter the correct postal code `);
    } else {
      dispatch(
        saveShippingAddress({ address, city, postalCode, country, phoneNumber })
      );
      history.push("/payment");
    }
  };

  return (
    <>
      <section className=" bg-gray-50 text-gray-600 body-font relative">
        <div className="container px-5 py-10 mx-auto">
          <div className="">
            <Link to="/cart">
              {" "}
              <FaBackward color="blue" />{" "}
            </Link>
          </div>
          <div className="flex flex-col items-center w-full mb-12">
            <h1 class="sm:text-3xl text-2xl  inline-flex  items-center font-medium title-font mb-4 text-gray-700">
              Shipping Ingo <BiLogInCircle className="mx-1" color="blue" />{" "}
            </h1>
          </div>
          <CheckOutSteps step1 step2 />

          <form onSubmit={submitHandler}>
            <div className="lg:w-1/2 md:w-2/3 mx-auto">
              <div className="flex flex-wrap -m-2">
                <div className="p-2 w-full">
                  <div className="relative">
                    <label
                      for="email"
                      className="leading-7 text-sm text-gray-600"
                    >
                      Address
                    </label>
                    <input
                      type="text"
                      required
                      value={address}
                      onChange={(e) => setAddress(e.target.value)}
                      autoFocus
                      className="w-full bg-gray-100 bg-opacity-50 rounded border border-gray-300 focus:border-indigo-500 focus:bg-white focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
                    />
                  </div>
                </div>

                <div className="p-2 w-full">
                  <div className="relative">
                    <label
                      for="email"
                      className="leading-7 text-sm text-gray-600"
                    >
                      Enter City
                    </label>
                    <input
                      type="text"
                      required
                      value={city}
                      onChange={(e) => setCity(e.target.value)}
                      className="w-full bg-gray-100 bg-opacity-50 rounded border border-gray-300 focus:border-indigo-500 focus:bg-white focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
                    />
                  </div>
                </div>

                <div className="p-2 w-full">
                  <div className="relative">
                    <label
                      for="email"
                      className="leading-7 text-sm text-gray-600"
                    >
                      Postal Code
                    </label>
                    <input
                      type="text"
                      required
                      value={postalCode}
                      onChange={(e) => setPostalCode(e.target.value)}
                      className="w-full bg-gray-100 bg-opacity-50 rounded border border-gray-300 focus:border-indigo-500 focus:bg-white focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
                    />
                  </div>
                </div>

                <div className="p-2 w-full">
                  <div className="relative">
                    <label
                      for="email"
                      className="leading-7 text-sm text-gray-600"
                    >
                      Country
                    </label>
                    <input
                      type="text"
                      required
                      disabled
                      value={country}
                      onChange={(e) => setCountry(e.target.value)}
                      className="w-full bg-gray-100 bg-opacity-50 rounded border border-gray-300 focus:border-indigo-500 focus:bg-white focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
                    />
                  </div>
                </div>

                <div className="p-2 w-full">
                  <div className="relative">
                    <label
                      for="email"
                      className="leading-7 text-sm text-gray-600"
                    >
                      Phone Number
                    </label>
                    <input
                      type="text"
                      placeholder="03XX-XXXXXXX"
                      pattern="03[0-9]{2}-(?!1234567)(?!1111111)(?!7654321)[0-9]{7}"
                      required
                      value={phoneNumber}
                      onChange={(e) => setphoneNumber(e.target.value)}
                      className="w-full bg-gray-100 bg-opacity-50 rounded border border-gray-300 focus:border-indigo-500 focus:bg-white focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
                    />
                  </div>
                </div>

                <div className=" p-2 w-full" onClick={submitHandler}>
                  <button
                    className="flex mx-auto text-white   bg-indigo-500 border-0 py-2 px-8 focus:outline-none hover:bg-indigo-600 rounded text-lg"
                  >
                    Continue
                  </button>
                </div>
              </div>
            </div>
          </form>
        </div>
      </section>
    </>
  );
};

export default Shipping;
